.class-booked-state {
    text-align: right;
}
.class-booked {
    color: #A00;
}
.class-free {
    color: #0A0;
}

.nowrap {
    white-space: nowrap;
}
.additional {
    position: relative;
    left: -50%;
    width: 0;
    overflow: visible;
    font-size: smaller;
    font-style: italic;
}

.baseline {
    vertical-align: baseline;
}
.relative {
    position: relative;
}