.app--main {
  min-height: 100%; /* transparent background should always stretch. */
  /* Center with margin. `vmin` should work even in IE */
  width: 85vmin;
  min-width: 50vw;
  margin: 0 auto;
  padding: 1em;
  /* . */
  font-family: Lora,
    /* -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto, */
    "Helvetica Neue",
    Arial,
    sans-serif;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    /* background: radial-gradient(closest-side, rgba(73, 42, 29, 1), transparent); */
    background: rgba(73, 42, 29, 0.25);
}

.app--heading {
  padding-top: 4em;
  padding-bottom: 4em;
  padding-top: 8vmin;
  padding-bottom: 8vmin;
  line-height: 1; /* TODO */
  text-align: center;
  color: #FFF;
  text-transform: capitalize;
  text-shadow: 0 0 3ex #300;
}
.app--heading .app--heading-title {
  font-size: 300%;
}

.app--banner-container {
  perspective: 24ex;
  /* perspective-origin: center; */
}
.app--heading.app--banner-container {
  padding-bottom: 2em;
  margin: 0 -3em; /* CALC: -(minimum margin of .app--main)/2 */
}

.app--banner {
  position: absolute;
  left: 50%;
  top: 40%;
  width: 18em;
  transform: translate(-50%,-50%) rotateX(30deg);
  z-index: -1;
}

/* for Sticky footer */
/* .Appcontainer {
  min-height: 100%;
  padding-bottom: 83px; /* CALC .app--footer:height */ /* TODO: ex *
} */

.app--footer {
  /* margin-top: -83px; /* CALC: - .app--footer:height * */
  padding: 3ex;
  font-size: 80%;
  font-weight: 400;
  text-align: center;
  background-color: hsl(18, 43%, 20%);
  opacity: .85;
  color: #FFF;
}

.app--module {
  position: relative; /* NOTE: for stemma-left etc. */
  padding: 6vmin;
  background-color: rgba(255, 255, 255, 0.85);
}

