/* https://www.paulirish.com/2012/box-sizing-border-box-ftw/ */
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

/* . */
body {
  padding: 0;
  overflow-y: hidden;
}

html, body, #root {
  height: 100%;
  margin:0;
}

/* By TAG type */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: .5rem;
}

/******** General Purpose ********/

.site--relative {
  position: relative;
}

.hidden {
  display: none !important;
}

.site--small {
  font-size: 85%;
}

.site--narrow {
  line-height: 1.25; /* aot 1.75 */
}

.site--bold {
  padding-left: .5ex;
  padding-right: .5ex;
  font-weight: bold;
}

.site--inline {
  display: inline-block;
  vertical-align: middle;
}

.site--indent {
  padding-left: 2ex;
}

/******** Tables ********/

.site--table {
  display: table;
  width: 100%;
  border-collapse: collapse;
  /* border-spacing: 2ex 0; Use padding instead to achieve styles that work for tables or normal divs (cfr .site--table.site--as-list)*/
}
.site--table.site--inline {
  display: inline-table;
  width: auto;
}
.site--table.site--as-list {
  width: auto;
}
/**** with actual <table> ****/
.site--table > tbody > tr > td {
  display: table-cell;
  border-bottom: .5ex solid hsla(34, 74%, 17%, .3);
  padding: .5ex;
}
.site--table.site--last-right-aligned > tbody > tr > td:last-child {
  font-size: 112.5%;
  padding-left: 2ex;
  text-align: right;
}
/**** with generic tags (div..) ****/
.site--table > .site--trow {
  display: table-row;
}
.site--tcell, .site--table > .site--trow > .site--tcell {
  display: table-cell;
}
.site--table.site--last-right-aligned > .site--trow > .site--tcell:last-child {
  font-size: 112.5%;
  padding-left: 2ex;
  text-align: right;
}

/******** Ratios , Justify/Stretch ********/
.site--of-2 {
  width: 50%;
}
.site--of-2.site--justify:first-child {
  text-align: right;
  padding-right: 1em;
}
.site--of-2.site--justify:nth-child(2) {
  text-align: left;
  padding-left: 1em;
}

/**** Stretch line ****/
.site--stretch > :first-child {
  width: 100%;
}
.site--stretch > .site--of-2:first-child {
  margin-right: -50%;
  text-align: left;
}
.site--stretch > .site--of-2:nth-child(2) {
  text-align: right;
}

/******** Lists ********/

.site--as-list {
  margin-top: 1em; /* cfr. ul:margin-top browser default */
}
.site--as-list > .site--as-list-item,
.site--table.site--as-list .site--tcell:first-child {
  padding-left: 2ex;
}
.site--as-list > .site--as-list-item:before,
.site--table.site--as-list .site--tcell:first-child::before {
  content: '\2022';
  padding-right: 1ex;
}

a, .site--internal-link {
  font-size: 112.5%;
  margin: -.5ex;
  padding: .5ex;
  cursor: pointer;
  color: hsl(214, 74%, 33%);
  text-decoration-line: underline;
}
a:hover, .site--internal-link:hover {
  background-image: linear-gradient(rgba(64,16,4,0.125), rgba(64,16,4,0.25));
}
.site--permanent .site--internal-link {
  margin: auto;
  display: inline-block;
  color: hsl(214, 74%, 77%);
  text-decoration-line: none;
}
.site--permanent .site--internal-link:hover {
  background-image: linear-gradient(hsla(12, 88%, 87%, 0.125), hsla(12, 88%, 87%, 0.25));
}

.site--offset > li {
  /* list-style: circle; */
  padding-left: 2ex;
}

.site--no-bullets {
  list-style: none;
  padding-left: 2ex; /* cfr. .site--indent */
}

/******** Buttons ********/

.site--button {
  padding: .75ex;
  text-align: center;
  vertical-align: middle;
  background: hsla(34, 74%, 67%,.75);
  cursor: pointer;
  border-radius: 1em;
}

/******** Misc. ********/

.site--ilist {
  display: inline-block;
}
.site--ilist:not(:last-child):after {
  content: ',\00A0';
}

/* Useful inside a text-align center / right block */
.site--wrap-left {
  display: inline-block;
  text-align: start;
}
.site--wrap-left.site--table {
  display: table;
}
.site--wrap-left.site--table.site--inline {
  display: inline-table;
}

.site--footnote-marker {
  margin-left: 1ex;
  cursor: pointer;
  color: hsl(214, 74%, 33%);
}
.site--footnote-marker:before {
  content: '*';
}
p.site--footnote {
  font-size: 85%;
}
p.site--footnote:nth-of-type(1):before {
  content: '(*)';
  padding-right: 1ex;
}
p.site--footnote:last-of-type {
  margin-bottom: 0; /* maybe set this on all p's */
}

.site--footnotes {
  margin-top: 2em;
}


/******** Site Theme ********/

#root {
  /* Controls the majority of lengths on the site. Capped at [iPod: 1024x768] via @media (see below) */
  font-size: 2.5vmin;
  overflow-y: scroll;
  background: /* linear-gradient(rgba(47, 23, 15, 0.65), rgba(47, 23, 15, 0.65)), */ url("./img/bg.jpg");
  background-position: center;
  background-size: cover;
}
@media(min-width: 768px) {
  #root {
    font-size: 19.2px;
  }
}

em {
  font-size: 112.5%;
}

p, .site--par {
  line-height: 1.75;
  margin-top: 1em;
}

.site--has-float:after {
  content: '';
  display: block;
  clear: both;
}

.site--text-primary {
  color: hsl(34, 74%, 67%);
}
