.nav--bar {
    display: table;
    border-collapse: collapse;
    width: 100%;
    text-align: center;
    font-family: 'Raleway';
}
@media (min-width: 768px) {
    .nav--bar {
        table-layout: fixed;
    }
}

.nav--link {
    display: table-cell;
    padding: 3ex 1ex;
    font-weight: 800;
    cursor: pointer;
    border-width: .2ex;
    border-style: solid;
    border-color: hsl(18, 43%, 15%);
    background: -webkit-linear-gradient(
        hsl(18, 43%, 25%),
        hsl(18, 43%, 20%)
    );
    background: linear-gradient(
        hsl(18, 43%, 25%),
        hsl(18, 43%, 20%)
    );
    color: #FFF;
    opacity: 0.75;
}
.nav--link:hover {
    opacity: 0.85;
}
.nav--link:active, .nav--link:focus {
    background: -webkit-linear-gradient(
        hsl(18, 43%, 20%),
        hsl(18, 43%, 25%)
    );
    background: linear-gradient(
        hsl(18, 43%, 20%),
        hsl(18, 43%, 25%)
    );
}
.nav--link:focus {
    outline: none;
}
.nav--link:disabled {
    cursor: default;
}
.nav--link.nav--active {
    color: hsl(34, 74%, 67%); /* REF .site--text-primary */
    /* :disabled behaviour */
    cursor: default;
    pointer-events: none;
}

