.icon--icon {
    font-size: 125%;
}
.icon--icon.icon--as-trow {
    display:table-row;
}
.icon--icon.icon--as-trow > span:first-child {
    display: table-cell;
}
.icon--icon:before {
    display: table-cell;
    text-align: right;
    padding-right: .75em;
}

.icon--icon.icon--copyright:before {
    content: '\A9';
}
.icon--icon.icon--home:before {
    content: '\1F3E0';
}
.icon--icon.icon--telephone:before {
    content: /* '\1F4DE'; */'\260E';
}
.icon--icon.icon--email:before {
    content: '\2709';
}